    <v-card>
      <v-card-text><h2>Bienvenido, a la plataforma GCEU</h2></v-card-text>
      <v-alert>
        En la parte superior izquierda de la pantalla encontrar un icono
        <v-icon>mdi-view-sequential</v-icon> con la cual podr navegar sobre las
        distintas ventanas que posee la aplicacin.
      </v-alert>
      <v-alert>
        En el icono <v-icon>mdi-account</v-icon> podr actualizar la informacin
        de su perfil. Si usted es un coordinador de prcticas podr gestionar
        las habilidades que los estudiantes desean desempear y que las empresas
        buscan encontrar en los practicantes.
      </v-alert>
      <v-alert>
        Usted desempea el cargo de {{ rol }} del programa {{ programa }} de la
        Universidad Mariana, recuerde que la informacin contenida en la
        plataforma est bajo responsabilidad de un grupo selecto de personas
        incluyendolo.
      </v-alert>
      <v-card-text><h2>Tips</h2></v-card-text>
      <v-alert>
        - No comparta sus credenciales de acceso. <br /><br />
        - Se le ha delegado la autorizacin a una parte de la informacin del
        proceso de gestin de prcticas, usted es el responsable de administrar
        esa informacin. <br /><br />
        - Antes de realizar cualquier accin sobre la plataforma asegrese de
        qu es lo que realmente desea realizar, por ejemplo: una vez aprobado
        los datos de un estudiante o de una empresa no hay manera de
        revertirlos.
        <br /><br />
        - En caso de cometer algn error o presentarse alguna inconsistencia de
        la informacin comunicarse inmediatamente con el rea encargada de la
        administracin de la plataforma.
        <br /><br />
        - Si el dispositivo en el que esta leyendo esta informacin es de uso
        pblico o usted no es el propietario, es recomendable que cierre sesin
        una vez finalice sus labores y no guarde contraseas. Para cerrar sesin
        haga click en el icono <v-icon>mdi-export</v-icon>.
      </v-alert>
    </v-card>
  </v-container>
</template>

<script>
import { OBTENER_DATOS_USUARIO } from "../../services/auth";

export default {
  name: "Welcome",
  data: () => ({
    rol: "",
    programa: "",
  }),
  methods: {
    async cargarInfoUsuario() {
      await OBTENER_DATOS_USUARIO().then((result) => {
        this.rol =
          result.data.rol === 1
            ? "Coordinador@ de prcticas"
            : result.data.rol === 2
            ? "Asesor/@"
            : "Super Admin";
        this.programa = result.data.programa;
      });
    },
  },
  async mounted() {
    await this.cargarInfoUsuario();
  },
};
</script>

